import React from "react";
import "./App.css";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  HashRouter,
} from "react-router-dom";
import Header from "./Components/Header/Header";
import Footer from "./Components/Footer/Footer";
import Accueil from "./Components/Accueil/Accueil";
import Apropos from "./Components/Apropos/Apropos";
import Cv from "./Components/Cv/Cv";
import Mesprojets from "./Components/Mesprojets/Mesprojets";

import "bootstrap/dist/css/bootstrap.min.css";

function App() {
  return (
    <HashRouter basename="/">
      <div className="header-nav-background">
        <Header />
      </div>
      <div className="main-content">
        <Switch>
          <Route path="/" component={Accueil} exact />
          <Route path="/apropos" component={Apropos} exact />
          <Route path="/cv" component={Cv} exact />
          <Route path="/mesprojets" component={Mesprojets} exact />
        </Switch>
      </div>
      <Footer />
    </HashRouter>
  );
}

export default App;
