import React from "react";
import "./accueil.css";
import Carousel from "react-bootstrap/Carousel";
import Fade from "react-reveal/Fade";
import { Col, Row } from "react-bootstrap";
import { FaDatabase } from "react-icons/fa";
import { CgScreen } from "react-icons/cg";
import { GiAutoRepair } from "react-icons/gi";

function Accueil() {
  return (
    <section>
      <div className="accueil-main-content">
        <Fade left>
          <Carousel interval={1000} className="carousel-main">
            <Carousel.Item>
              <a href="/sites-externe/mikapiscine/index.html">
                <img
                  className="d-block w-100"
                  src="/img/mika-3.png"
                  alt="First slide"
                />
              </a>
            </Carousel.Item>
            <Carousel.Item>
              <a href="/sites-externe/sophiephoto/index.html">
                <img
                  className="d-block w-100"
                  src="/img/sophie-3.png"
                  alt="second slide"
                />
              </a>
            </Carousel.Item>
            <Carousel.Item>
              <a href="/sites-externe/jashoppan/index.html">
                <img
                  className="d-block w-100"
                  src="/img/jashoppan-3.png"
                  alt="Third slide"
                />
              </a>
            </Carousel.Item>
          </Carousel>
        </Fade>
      </div>
      <div className="accueil-content">
        <Row>
          <Col>
            <Fade top>
              <h3>
                <CgScreen /> Front-end
              </h3>
            </Fade>
            <Fade bottom>
              <p>HTML 5</p>
              <hr />
              <p>CSS 3</p>
              <hr />
              <p>Javascript</p>
              <hr />
              <p>React JS</p>
            </Fade>
          </Col>
          <Col>
            <Fade top>
              <h3>
                <FaDatabase /> Back-end
              </h3>
            </Fade>
            <Fade bottom>
              <p>Node.Js</p>
              <hr />
              <p>Express.Js</p>
              <hr />
              <p>Mongo DB</p>
              <hr />
              <p>Mongoose</p>
            </Fade>
          </Col>
          <Col>
            <Fade top>
              <h3>
                <GiAutoRepair /> Divers
              </h3>
            </Fade>
            <Fade bottom>
              <p>Robot 3T</p>
              <hr />
              <p>Postman</p>
              <hr />
              <p>Bootstrap</p>
              <hr />
              <p>Vscode</p>
            </Fade>
          </Col>
        </Row>
      </div>
    </section>
  );
}

export default Accueil;
