import React, { useState } from "react";
import "./header.css";
import { Link } from "react-router-dom";
import { HashLink } from "react-router-hash-link";
import { CgNotes } from "react-icons/cg";
import { FiMail } from "react-icons/fi";
import { BsPeopleCircle } from "react-icons/bs";
import { HiOutlineHome } from "react-icons/hi";
import { FiGithub } from "react-icons/fi";
import { GiHamburgerMenu } from "react-icons/gi";
import Nav from "react-bootstrap/Nav";
import Modal from "react-bootstrap/Modal";
import Button from "react-bootstrap/Button";

function Header() {
  const [displayNav, setDisplayNav] = useState(false);
  const [showModal, setShowModal] = useState(false);
  const handleClose = () => setShowModal(false);
  const handleShow = () => setShowModal(true);

  function showMenuMobile() {
    if (displayNav) {
      return (
        <div className="menu-mobile-global">
          <Nav
            onClick={() => {
              setDisplayNav(false);
            }}
          >
            <ul class="nav-menu">
              <li>
                <Link to="/">
                  <HiOutlineHome />
                  Accueil
                </Link>
              </li>
              <li>
                <Link to="/mesprojets">
                  <FiGithub />
                  Mes Projets
                </Link>
              </li>
              <li>
                <Link to="/cv">
                  <CgNotes />
                  Cv
                </Link>
              </li>
              <li>
                <Link to="/apropos">
                  <BsPeopleCircle />A propos
                </Link>
              </li>
              <li>
                <HashLink
                  onClick={() => {
                    setShowModal(!showModal);
                  }}
                >
                  <FiMail />
                  Contact
                </HashLink>
              </li>
            </ul>
          </Nav>
        </div>
      );
    }
  }

  function displayModal() {
    if (showModal) {
      return;
    }
  }

  return (
    <header>
      <GiHamburgerMenu
        className="menu-mobile"
        onClick={() => {
          setDisplayNav(!displayNav);
        }}
      />
      <img id="logo" src="/img/logorc-seul.png" alt="logo" />
      <h1 id="logo-name">uffa Cyril</h1>
      <div class="container">
        <nav id="nav-menu-container">
          <ul class="nav-menu">
            <li>
              <Link to="/">
                <HiOutlineHome />
                Accueil
              </Link>
            </li>
            <li>
              <Link to="/mesprojets">
                <FiGithub />
                Mes Projets
              </Link>
            </li>
            <li>
              <Link to="/cv">
                <CgNotes />
                Cv
              </Link>
            </li>
            <li>
              <Link to="/apropos">
                <BsPeopleCircle />A propos
              </Link>
            </li>
            <li>
              <HashLink
                onClick={() => {
                  setShowModal(!showModal);
                }}
              >
                <FiMail />
                Contact
              </HashLink>
            </li>
          </ul>
        </nav>
      </div>{" "}
      {showMenuMobile()}
      <div>
        <Modal show={showModal} onHide={handleClose}>
          <Modal.Header closeButton>
            <Modal.Title id="modal-title">Contact</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <form
              className="header-form"
              action="mailto:skyler@ruffa-karting.frsubject=form"
              name="envoi"
              method="POST"
              enctype="text/plain"
            >
              <div className="modal-label">
                <label htmlFor="nom">Nom:</label>
                <input
                  type="text"
                  placeholder="Nom"
                  name="nom "
                  id="nom"
                  maxlength="20"
                  required
                />
                <br />
                <label htmlFor="email"> Email: </label>
                <input
                  type="email"
                  placeholder="Email"
                  name="email "
                  id="email"
                  maxlength="20"
                  required
                />
                <br />
                <label htmlFor="tel"> Tél: </label>
                <input
                  type="tel"
                  placeholder="Téléphone"
                  name="tel "
                  id="tel"
                  required
                />
                <br />
                <label className="message" htmlFor="message">
                  Message:
                </label>
                <textarea
                  type="text"
                  placeholder="Message"
                  name="message "
                  id="message"
                ></textarea>
                <br />
                <div>
                  <button id="button-envoyer" type="submit" value="Envoyer">
                    Envoyer
                  </button>
                </div>
              </div>
            </form>
          </Modal.Body>
          <Modal.Footer>
            <p>Vous pouvez également me contacter au: 06.52.72.68.11</p>
            <Button id="button-close" variant="secondary" onClick={handleClose}>
              Close
            </Button>
          </Modal.Footer>
        </Modal>
      </div>
    </header>
  );
}

export default Header;
